import React, {useState, useRef, useEffect} from 'react';

import {useDispatch, useSelector} from "react-redux";
import {logOutUser} from "../../_actions/userAction";
import {Link} from "react-router-dom";

import NavBarSection from "./HeaderFrame/NavBarSection";
import NavBarWrapper from "./HeaderFrame/NavBarWrapper";
import NavbarButton from "./HeaderFrame/NavbarButton";




const Header = ()=> {
    const users = useSelector(state => state.userReducer);
    const dispatch = useDispatch();
    const onLogOut = () => {
        dispatch(logOutUser());
        console.log(users.currentUser);
    }

    const [visible, setVisible] = useState(false);
    const [shadowClassName, setShadow] = useState("");


    const showDrawer = () => {
        setVisible(true)
    };

    const onClose = () => {
        setVisible(false)
    };
    
    // NavBar 위치에 따라 그림자 효과 주기
    const onScroll = () => {
        window.scrollY > 10 ? setShadow("navbar--dark") :  setShadow("");
    }

    useEffect( ()=> {
        document.addEventListener("scroll", ()=> {
            onScroll();
            return ()=> document.removeEventListener("scroll", onScroll);
        })
    }, []);

    return (
        <NavBarWrapper className={shadowClassName}>
            <NavBarSection className = "navbar__logo" flex={2} justifyContent={"center"}  >
                <Link to = "/"><i className="fas fa-puzzle-piece"></i> <span>BeneBean's Coding</span></Link>
            </NavBarSection>
            <NavBarSection className = "navbar__container" flex={8} justifyContent={"center"}>
                <NavBarSection flex={2} ulList ulStyle={"padding: 0 60px"}>
                    <ul>
                        <Link to = "/"><li>Home</li></Link>
                        <Link to = "/"><li>Community</li></Link>
                        <Link to = "/"><li>CrawledInfo</li></Link>
                        <Link to = "/"><li>History</li></Link>
                        <Link to = "/uploadproduct"><li>Upload</li></Link>
                    </ul>
                </NavBarSection>
                <NavBarSection flex={1} ulList ulStyle={"padding: 0 40px"}>
                    <ul>
                        <li>
                            {
                                !users.isLoggedIn
                                ? <NavbarButton to = "/login">Log In</NavbarButton>
                                : <NavbarButton to = "/" onClick={onLogOut}>Log Out</NavbarButton>
                            }
                        </li>
                        <li>
                            {
                                !users.isLoggedIn
                                && <NavbarButton to = "/register">Sign In</NavbarButton>
                            }
                        </li>
                        <li>
                            <a target="_blank" rel="noopener noreferrer" href="https://github.com/posungkim">
                                <i className="fab fa-github"></i>
                            </a>
                        </li>
                        <li>
                            <a target="_blank" rel="noopener noreferrer" href="https://www.facebook.com/posung.kim">
                                <i className="fab fa-facebook-square"></i>
                            </a>
                        </li>
                    </ul>
                </NavBarSection>
            </NavBarSection>
        </NavBarWrapper>
    )
}

export default Header;